import './Contact.css'
import {useState} from 'react';


function Form(){

    const [name,setname] = useState('');
    const [addr,setaddr] = useState('');
    const [gend,setgend] = useState('Please Select Gender');

    function submitHandler(event){
        event.preventDefault();
        localStorage.name = name;
        localStorage.addr = addr;
        localStorage.gend = gend;

    }

    return(
        <>
        <form onSubmit={submitHandler}>
            <label>Name: </label>
            <input className='name' type="text" value={name} 
            onChange={(e)=>setname(e.target.value)} /><br/><br/>

            <label>Address: </label>
            <textarea className='addr' value={addr} 
            onChange={(e)=>setaddr(e.target.value)}></textarea><br/><br/>

            <label>Gender: </label>
            <select className='gend' value={gend} 
            onChange={(e)=>setgend(e.target.value)}>
                <option value="Male">Male</option>
                <option value="Female">Female</option>
            </select><br/><br/>
            <input type="submit" value="submit form"/>
        </form><br/>
        </>
    )
}
export default Form;